<template>
  <v-container>
    <v-card>
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="desserts"
        :single-select="true"
        item-key="room"
        show-select
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar
            flat
            color="white"
          >
            <v-dialog
              v-model="dialog"
              max-width="500px"
            >
              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Номер:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedItem.room"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Категория:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedItem.category"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Мест:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedItem.place"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Корпус:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedItem.housing"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Этаж:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedItem.flat"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Статус:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedItem.status"
                        />
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    text
                    @click="close"
                  >Отменить</v-btn>
                  <v-btn
                    color="primary"
                    text
                    @click="save"
                  >Сохранить</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog
              v-model="categoryDialog"
              max-width="500px"
            >
              <v-card>
                <v-card-title><span class="headline">Добавить категорию</span></v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Название:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedCategory.name"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Основных мест:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedCategory.mainPlaces"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Дети:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedCategory.children"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Доп место:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedCategory.additionalRooms"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Цена за одноместное размещение:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedCategory.singleOccupancyPrice"
                        />
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="4"
                      >
                        <p class="title">Создать номера для категории:</p>
                      </v-col>
                      <v-col
                        class="pt-0 pb-0"
                        cols="8"
                      >
                        <v-text-field
                          dense
                          v-model="editedCategory.createRoomCategory"
                        />
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    text
                    @click="closeCategory"
                  >Отменить</v-btn>
                  <v-btn
                    color="primary"
                    text
                    @click="saveCategory"
                  >Сохранить</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
      <div class="pt-2 pb-2">
        <v-btn
          color="primary"
          class="mr-2"
          @click="createRoom"
        >Добавить номер</v-btn>
        <v-btn
          color="primary"
          @click="createCategory"
        >Добавить категорию</v-btn>
      </div>
    </v-card>
  </v-container>
</template>

<script>
  import Vue from 'vue'
  export default {
    name: 'RoomsFund',
    data () {
      return {
        singleSelect: false,
        selected: [],
        dialog: false,
        categoryDialog: false,
        headers: [
          {
            text: 'Выбрать',
            align: 'start',
            sortable: false,
            value: 'select',
          },
          { text: 'Номер', value: 'room' },
          { text: 'Категория', value: 'category' },
          { text: 'Мест', value: 'place' },
          { text: 'Корпус', value: 'housing' },
          { text: 'Этаж', value: 'flat' },
          { text: 'Статус', value: 'status' },
          { text: 'Действие', value: 'actions' },
        ],
        desserts: [],
        categories: [],
        editedIndex: -1,
        editedCategoryIndex: -1,
        editedItem: {
          select: null,
          room: 0,
          category: '',
          place: '0',
          housing: 0,
          flat: 0,
          status: '',
        },
        editedCategory: {
          name: null,
          mainPlaces: null,
          children: null,
          additionalRooms: null,
          singleOccupancyPrice: null,
          createRoomCategory: null,
        },
        defaultCategory: {
          name: null,
          mainPlaces: null,
          children: null,
          additionalRooms: null,
          singleOccupancyPrice: null,
          createRoomCategory: null,
        },
        defaultItem: {
          select: null,
          room: 0,
          category: '',
          place: '0',
          housing: 0,
          flat: 0,
          status: '',
        },
      }
    },
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Удалить номер' : this.editedIndex === 1 ? 'Создать номер' : 'Редактировать номер'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    created () {
      this.initialize()
    },
    methods: {
      initialize () {
        this.desserts = [
          {
            select: null,
            room: 100,
            category: 'Люкс',
            place: '2+1',
            housing: 1,
            flat: 1,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 101,
            category: 'Полулюкс',
            place: '2+1',
            housing: 1,
            flat: 1,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 102,
            category: 'Стандарт',
            place: '2+1',
            housing: 1,
            flat: 3,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 103,
            category: 'Люкс',
            place: '1',
            housing: 1,
            flat: 1,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 104,
            category: 'Люкс',
            place: '2+1',
            housing: 1,
            flat: 1,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 105,
            category: 'Полулюкс',
            place: '1',
            housing: 1,
            flat: 4,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 106,
            category: 'Стандарт',
            place: '1',
            housing: 1,
            flat: 1,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 107,
            category: 'Люкс',
            place: '2+1',
            housing: 1,
            flat: 1,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 108,
            category: 'Стандарт',
            place: '2+1',
            housing: null,
            flat: 3,
            status: 'Грязный',
            action: null,
          },
          {
            select: null,
            room: 109,
            category: 'Стандарт',
            place: '2+1',
            housing: 1,
            flat: 1,
            status: 'Грязный',
            action: null,
          },
        ]
        this.categories = [
          {
            name: null,
            mainPlaces: null,
            children: null,
            additionalRooms: null,
            singleOccupancyPrice: null,
            createRoomCategory: null,
          },
        ]
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        confirm('Вы точно хотите удалить этот запись?') && this.desserts.splice(index, 1)
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },

      saveCategory () {
        if (this.editedCategoryIndex > -1) {
          Vue.set(this.categories, this.editedCategoryIndex, this.editedCategory)
        } else {
          this.desserts.push(this.editedCategory)
        }
        this.closeCategory()
      },

      closeCategory () {
        this.categoryDialog = false
        this.$nextTick(() => {
          this.editedCategory = Object.assign({}, this.defaultCategory)
          this.editedCategoryIndex = -1
        })
      },

      createCategory () {
        this.categoryDialog = true
        this.$nextTick(() => {
          this.editedCategory = Object.assign({}, this.defaultCategory)
          this.editedCategoryIndex = 1
        })
      },
      createRoom () {
        this.dialog = true
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = 1
        })
      },
    },
  }
</script>

<style scoped>
  .title {
    font-size: 18px !important;
    font-weight: 500 !important;
    color: orange !important;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
</style>
